<template>
  <d2-container>
    <template slot="header">
      <el-select
        v-model="serviceName"
        placeholder="请选择服务"
        size="small"
        @change="changeService"
        filterable
        clearable
      >
        <el-option
          v-for="item in serviceOptions"
          :key="item"
          :label="item"
          :value="item"
        >
        </el-option>
      </el-select>
      <el-select
        v-model="instanceValue"
        placeholder="请选择您的实例"
        size="small"
        @change="changeInstance"
        filterable
        clearable
        style="width: 200px"
      >
        <el-option
          v-for="(item, index) in instanceOptions"
          :key="index"
          :label="item.instanceId.split('#')[0]"
          :value="item.instanceId"
        >
        </el-option>
      </el-select>
      <div class="topTime" style="float: right">
        <strong>时间：</strong>
        <span style="padding: 3px 0">{{ serverData.crt_time }}</span>
      </div>
    </template>

    <el-card class="box-card">
      <el-row :gutter="20">
        <el-col :span="12">
          <el-form label-position="left" class="demo-table-expand" size="mini">
            <el-form-item label="N9A接口接收GTP包数"><span>{{ serverData.upf_n9a_inc_pkt }}</span></el-form-item>
            <el-form-item label="N9A接口发送GTP包数"><span>{{ serverData.upf_n9a_og_pkt }}</span></el-form-item>
            <el-form-item label="N9A接口接收GTP包字节数"><span>{{ serverData.upf_n9a_inc_oct }}</span></el-form-item>
            <el-form-item label="N9A接口发送GTP包字节数"><span>{{ serverData.upf_n9a_og_oct }}</span></el-form-item>
            <el-form-item label="N9A接口接收错误GTP包数"><span>{{ serverData.upf_n9a_disc_pkt }}</span></el-form-item>
          </el-form>
        </el-col>
        <el-col :span="12">
          <el-form label-position="left" class="demo-table-expand" size="mini">
            <el-form-item label="N9C接口接收GTP包数"><span>{{ serverData.upf_n9c_inc_pkt }}</span></el-form-item>
            <el-form-item label="N9C接口发送GTP包数"><span>{{ serverData.upf_n9c_og_pkt }}</span></el-form-item>
            <el-form-item label="N9C接口接收GTP包字节数"><span>{{ serverData.upf_n9c_inc_oct }}</span></el-form-item>
            <el-form-item label="N9C接口发送GTP包字节数"><span>{{ serverData.upf_n9c_og_oct }}</span></el-form-item>
            <el-form-item label="N9C接口接收错误GTP包数"><span>{{ serverData.upf_n9c_disc_pkt }}</span></el-form-item>
          </el-form>
        </el-col>
      </el-row>
    </el-card>

    <el-tabs v-model="activeName" type="card" @tab-click="handleClick">
      <el-tab-pane v-for="(item, index) in tabPaneList" :key="index" :label="item.tabLabel" :name="item.tabLabel">
        <el-table :data="serverData[item.dataKey]" style="width: 100%">
          <el-table-column prop="id" label="ID" width="180">
          </el-table-column>
          <el-table-column prop="dnn_name" label="dnn_name" width="180">
          </el-table-column>
          <el-table-column :prop="item.columKey" label="次数">
          </el-table-column>
        </el-table>
      </el-tab-pane>
    </el-tabs>
  </d2-container>
</template>

<script>
import {
  fetchGetServiceList,
  fetchGetInstanceList,
  fetchGetPerformN9,
} from "@/api/demo/index";
export default {
  name: "performN9",
  data() {
    return {
      activeName: "分DNN的N9A接口接收GTP包数",
      serviceName: "",
      serviceOptions: [],
      instanceOptions: [], //upf下面的实例
      instanceValue: "", //当前选中的实例
      isFirst: true,
      loading: false,
      serverData: {},
      tabPaneList: [
        { tabLabel:'分DNN的N9A接口接收GTP包数', dataKey: 'n9aIncPktDnnList', columKey: 'n9a_inc_pkt' },
        { tabLabel:'分DNN的N9A接口发送GTP包数', dataKey: 'n9aOgPktDnnList', columKey: 'n9a_og_pkt' },
        { tabLabel:'分DNN的N9A接口接收GTP包字节数', dataKey: 'n9aIncOctDnnList', columKey: 'n9a_inc_oct' },
        { tabLabel:'分DNN的N9A接口发送GTP包字节数', dataKey: 'n9aOgOctDnnList', columKey: 'n9a_og_oct' },
        { tabLabel:'分DNN的N9A接口接收错误GTP包数', dataKey: 'n9aDiscPktDnnList', columKey: 'n9a_disc_pkt' },

        { tabLabel:'分DNN的N9C接口接收GTP包数', dataKey: 'n9cIncPktDnnList', columKey: 'n9c_inc_pkt' },
        { tabLabel:'分DNN的N9C接口发送GTP包数', dataKey: 'n9cOgPktDnnList', columKey: 'n9c_og_pkt' },
        { tabLabel:'分DNN的N9C接口接收GTP包字节数', dataKey: 'n9cIncOctDnnList', columKey: 'n9c_inc_oct' },
        { tabLabel:'分DNN的N9C接口发送GTP包字节数', dataKey: 'n9cOgOctDnnList', columKey: 'n9c_og_oct' },
        { tabLabel:'分DNN的N9C接口接收错误GTP包数', dataKey: 'n9cDiscPktDnnList', columKey: 'n9c_disc_pkt' }
      ]
    };
  },
  mounted() {
    this.getService();
  },
  methods: {
    handleClick(tab, event) {
      console.log(tab, event);
    },
    // 获取服务列表
    getService() {
      fetchGetServiceList({
        pageNo: 1,
        pageSize: 50,
      })
        .then((response) => {
          console.log(response);
          this.serviceOptions = response.doms.filter(
            (item) => !item.startsWith("ace")
          );
          if (this.serviceOptions.length > 0) {
            this.serviceName = this.serviceOptions[0];
            this.getServiceInstance(this.serviceName);
          }
        })
        .catch((e) => {
          console.log(e);
          this.$alert(e, "网络请求发生错误", {
            confirmButtonText: "确定",
            callback: (action) => {},
          });
        });
    },
    // 选择服务
    changeService(val) {
      if (!val) {
        return;
      }
      this.getServiceInstance(val);
    },
    // 选择实例
    changeInstance(val) {
      // 图表置空
      this.serverData = {};

      // 获取告警数据
      this.getData();
    },
    //获取服务下面的实例
    getServiceInstance(serviceName) {
      fetchGetInstanceList({ serviceName: serviceName })
        .then((res) => {
          this.instanceOptions = res.hosts;
          if (this.instanceOptions.length > 0) {
            this.instanceValue = this.instanceOptions[0].instanceId;
            this.changeInstance(this.instanceValue);
          }
        })
        .catch((e) => {
          console.log(e);
          this.$alert(e, "网络请求发生错误", {
            confirmButtonText: "确定",
            callback: (action) => {},
          });
        });
    },
    // 获取数据
    getData() {
      this.loading = true;
      fetchGetPerformN9({
        request_type: "n9 perform",
        instance_id: this.instanceValue,
        sequence_no: 3356,
      })
        .then((response) => {
          this.loading = false;
          console.log(response);
          if ((response != null) & (response != [])) {
            this.serverData = response;
          }
        })
        .catch((e) => {
          this.loading = false;
          console.log(e);
          this.$alert(e, "网络请求发生错误", {
            confirmButtonText: "确定",
            callback: (action) => {},
          });
        });
    },
  },
};
</script>

